<template>
  <swiper :options="swiperOption">
    <swiper-slide v-for="(slide, index) in swiperSlides" :key="index">
      <img :src="slide">
    </swiper-slide>
    <div class="swiper-pagination" slot="pagination"></div>
  </swiper>
</template>

<script>
  export default {
    name: 'carrousel',
    props:['swiperSlides'],
    data() {
      return {
        swiperOption: {
          pagination: {
            el: '.swiper-pagination',
            autoplay:1000,
            setWrapperSize:true,
            pagination:'.swiper-pagination',
            observeParents:true
          }
        },
        // swiperSlides: [1, 2, 3, 4, 5]
      }
    }
  }
</script>
<style scoped>
  img{
    width: 100%;
  }
</style>



